<template>
  <div>
    <v-app-bar app color="#16BAA1" dark>
      <v-btn v-if="showbackbutton" text @click="goBack()">
        <v-icon>mdi-arrow-left-bold</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn text @click="logout">
        <span class="mr-2">signOut</span>
      </v-btn>
    </v-app-bar>
  </div>
</template>
<script>
import firebase from "firebase";

export default {
  name: "NavBar",
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    },
    goBack() {
      this.$router.push("/home")
    }
  },
  props: {
    showbackbutton: {
      type: Boolean,
      default: false
    },
    userdata: null,
    roomtitle: ""
  }
};
</script>
